.search-result{
	&__count{
		text-align: center;
		padding: 10px 0 40px;
		font-size: 22px;
		line-height: 136%;
		color: #414141;
	}
	&__form{
		width: 990px;
		margin: 0 auto 60px;
		input[type='text']{
			width: 100%;
		}
	}
	&__container{
		.container{
			display: flex;
			flex-wrap: wrap;
		}
	}
	&__btn{
		justify-content: center;
		display: flex;
		padding: 30px 0 70px;
		position: relative;
		.sbmt--red-border{
			background-color: #fff;
			position: relative;
			z-index: 10;
			text-transform: uppercase;
			&:hover{
				background-color: #EF4949;
			}
		}
		&::before{
			content: '';
			width: 100%;
			height: 1px;
			background-color: #D8DFE2;
			left: 0;
			top: 56.5px;
			position: absolute;
			z-index: 1;
		}
	}
	.news{
		.search-result__btn{
			background-color: #F1F5F6;
		}
		.main-article{
			padding-top: 0px;
		}
	}
}